import React from 'react';
import { shape, object, arrayOf, bool, func, string } from 'prop-types';
import Link from 'gatsby-link';
import cx from 'classnames';

import { Icon } from '../../ui';
import Navigation from '../../components/Navigation/Navigation';

import './Header.scss';

const getNavItems = (items) =>
	items.filter(i => {
		const scope = i.node.frontmatter.scope;
		return scope && scope.indexOf('HEADER') > -1
	});

const Header = ({ data, isTop, withNav, path, navVisible, showNav }) => {
	const shadowClass = cx({
		'header__shadow': true,
		'header__shadow--visible': !isTop
	});

	const menuButton = 
		<i 	className="material-icons"
			onClick={showNav.bind(this, true)} >
			menu
		</i>;

	const closeButton = 
		<i 	className="material-icons"
			onClick={showNav.bind(this, false)} >
			close
		</i>;

	return (
		<div>
			<div className="header__container">
				<div className="header__content">
					<Link to="/"
						className="logo__container">
						<Icon image="logo" />
					</Link>

					{
						withNav &&
						(
							<Navigation
								path={path}
								pages={getNavItems(data.allMarkdownRemark.edges)}
								isTop={isTop}
							/>
						)
					}

					<div className="header__mobileMenu">
						{
							navVisible
							? closeButton
							: menuButton
						}
					</div>
				</div>

			</div>
			
			<div className={shadowClass} />
		</div>
	);
}

Header.propTypes = {
	data: shape({
		allMarkDownRemark: shape({
			edges: arrayOf(object)
		})
	}),
	isTop: bool,
	withNav: bool,
	path: string
}

export default Header


