import React from 'react';
import { shape, object, arrayOf, bool, func, string } from 'prop-types';
import Link from 'gatsby-link';
import cx from 'classnames';

import { Icon, Divider } from '../../ui';
import { SignupForm } from '../../components/SignupForm';

import './MobileNav.scss';

const getNavItems = (items) =>
	items.filter(i => {
		const scope = i.node.frontmatter.scope;
		return scope && scope.indexOf('HEADER') > -1
	});

export default class MobileNav extends React.Component {
	componentDidMount() {
		document.body.style.overflow = 'hidden';
		document.addEventListener('touchmove', this.handleScroll, false);
		document.addEventListener('scroll', this.handleScroll, false);
	}

	componentWillUnmount() {
		document.body.style.overflow = 'visible';
		document.removeEventListener('touchmove', this.handleScroll, false);
		document.removeEventListener('scroll', this.handleScroll, false);
	}

	handleClick(event) {
		event.stopPropagation();
	}

	handleScroll = (e) => {
		e.preventDefault();
	}

	render() {
		const { data, showNav } = this.props;

		const list = [];

		getNavItems(data.allMarkdownRemark.edges).forEach((item, index) => {
			list.push(
				<Link
					to={item.node.fields.slug}
					className="copytext-large"
					key={index} >
					{item.node.frontmatter.title}
				</Link>
			);

			list.push(<Divider key={index + 'd'} />);
		});

		return (
			<div className="mobileNav__container"
				onClick={showNav.bind(this, false)}>
				<div className="mobileNav__content"
					onClick={this.handleClick}>
					<div />

					<div className="mobileNav__items">
						<Divider />
						{list}
					</div>

					<div className="mobileNav__form">
						<SignupForm secondarySubmitBtn />
					</div>
				</div>

				<div className="mobileNav__shadow" />
			</div>
		);
	}
};

MobileNav.propTypes = {
	data: shape({
		allMarkDownRemark: shape({
			edges: arrayOf(object)
		})
	}),
	isTop: bool,
	withNav: bool,
	path: string,
	showNav: func,
}



